<template>
  <div>
    <BasePanel>
      <template #panel-head>
        <router-link to="/">主页</router-link>
        /
        <span>发布话题</span>
      </template>
      <template #panel-content>
        <div class="create-wrap">
          <span>选择板块: </span>
          <el-select v-model="tab" placeholder="请选择">
            <el-option label="分享" value="share">分享</el-option>
            <el-option label="招聘" value="job">招聘</el-option>
            <el-option label="问答" value="ask">问答</el-option>
            <el-option label="客户端测试" value="dev">客户端测试</el-option>
          </el-select>
          <el-input v-model="title" placeholder="标题字数 10 字以上"></el-input>
          <el-input
            type="textarea"
            :rows="28"
            placeholder="请输入内容"
            v-model="content"
            resize="none"
          >
          </el-input>
          <el-button type="primary" size="small" @click="submit">提交</el-button>
        </div>
      </template>
    </BasePanel>
  </div>
</template>

<script>
import BasePanel from "../components/layout/BasePanel.vue";
import {create} from '../plugins/api'
export default {
  components: { BasePanel },
  data() {
    return {
      tab: "",
      title: "",
      content: "",
    };
  },
  methods:{
    async submit(){
      const accesstoken = sessionStorage.getItem('accesstoken')
      const {tab,title,content} = this
      const res = await create(accesstoken,title,tab,content)
      this.$router.push(`/topic/${res.topic_id}`)
    }
  }
};
</script>

<style lang='less'>
.create-wrap{
  width: 1400px;
  margin: 0 auto;
  padding: 20px 15px;
  .el-select,.el-input{
    margin-bottom: 10px;
  }
  .el-textarea{
    margin-bottom: 10px;
  }
  // .el-textarea__inner:focus{
  //   border-color: red;
  // }
}
</style>